//$main-background: black;
$main-background: #262524;
$code-background: darken($main-background, 10);
$main-grey: #949494;
$code-grey: lighten($main-grey, 20);
$dark-rule: #303030;
$main-gold: #BFAD8A;
$main-light-gold: lighten($main-gold, 20);
$base-font-size: 15px;

@font-face {
    font-family: "DIN Alternate Bold";
    src: url("../fonts/DIN Alternate Bold.otf")
            format("opentype");
}

@font-face {
    font-family: "Microgramma";
    src: url("../fonts/mg.ttf")
         format("truetype");
}

@font-face {
    font-family: "Microgramma Bold";
    src: url("../fonts/mg_b.otf")
         format("opentype");
}

@mixin title-font {
    font-family: 'Microgramma', "Helvetica Neue", Helvetica, Arial, sans-serif;
    /*text-transform: uppercase;*/
    color: $main-gold;
}

@mixin title-bold-font {
    font-family: 'Microgramma Bold', "Helvetica Neue", Helvetica, Arial, sans-serif;
    text-transform: uppercase;
    color: $main-gold;
}

@mixin post-title-font {
    color: $main-gold;
}

@mixin main-font {
    font-family: Georgia, Arial, sans-serif;
}

@mixin caption-in-image($mult) {
    font-size: $base-font-size * $mult;
    color: white;
    text-shadow: 0 0 8px black;
}

@mixin bottom {
    margin-bottom: 25px;
}

body, nav.navbar {
    color: $main-grey;
    background-color: $main-background;

    @include main-font;
    font-size: $base-font-size;

    blockquote {
        @include main-font;
        font-size: $base-font-size;
        font-style: italic;
        border-left: 2px solid $dark-rule;
    }
}

nav.navbar {
    border-bottom: 0 /*1px dotted $main-grey*/;

    a.navbar-brand {
        @include title-font;
        font-size: $base-font-size * 1.8;
        border-bottom: 0;

        .main {
            @include title-bold-font;
            color: $main-light-gold;
        }

        &:hover {
            color: #CAC9C8;
        }
    }
}

div.main-head h1,
div.header-block h1 {
    margin-top: 0;
    font-size: $base-font-size * 1.8;
    border-bottom: 1px solid $main-grey;
}

div.post-splash, div.project-splash, div.block-image {
    img {
        margin: 0 auto;
        max-height: 400px;
    }

    // BROKEN margin-top: 100px;
    @include bottom;
}

div.main-head h1 {
    @include title-font;
    @include bottom;
}

div.header-block h1 {
    @include post-title-font;
    @include bottom;
}

div.inner-section-header h2 {
    @include post-title-font;
    @include bottom;
    font-size: $base-font-size * 1.5;
}

div.embed-responsive {
    @include bottom;
}

@mixin div-overlay {
    position: absolute;
    margin: 0;
    /*margin-top: 0;
    margin-left: 0;*/
    left: 0; /***/
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}

div.post-splash, div.project-splash, div.block-image, div.brick {
    div.image-area > div.overlay {
        @include div-overlay;
    }

    div.image-area:hover > div.overlay {
        display: block;
    }
}

div.post-splash, div.project-splash, div.block-image {
    div.image-area {
        position: relative;
        cursor: zoom-in;

        div.overlay {
            pointer-events: none;
        }
    }
}

@mixin mini-head {
    color: darken($main-grey, 20%);
    font-size: $base-font-size * 0.8;
}

div.brick {
    position: relative;         // Allow h2 absolute to work.

    div.image-area {
        position: relative;

        h2 {
            position: absolute;
        }

        div.blank {
            background-color: #303030;
            height: 100px;
        }
    }

    div.caption {
        margin-top: 5px;
        line-height: 1.25em;
    }

    div.date {
        @include mini-head;
        text-align: right;
        margin-bottom: 5px;
    }
}

div#p-index div.brick div.image-area h2 {
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    @include caption-in-image(1.25);
}

div#p-tag_index div.brick div.image-area h2 {
    margin-top: 8px;
    margin-left: 8px;
    margin-right: 8px;
    @include caption-in-image(1);
}

div#p-page_for_md {
    h1 {
        @include title-font;
        font-size: $base-font-size * 1.4;
        margin-top: 5px;
    }

    ul {
        margin-bottom: 10px;
    }
}

h3.header, h3.tag-list {
    @include mini-head;
    //text-align: right;

    span.bar {
        font-weight: bold;
    }
}

h2.subheading {
    margin-top: -15px;
    margin-bottom: 20px;
    font-size: $base-font-size * 1.25;

    a {
        font-variant: small-caps;
    }
}

h3.header {
    margin-top: -15px;
    margin-bottom: 20px;
}

h3.tag-list {
    margin-top: 0;
    margin-bottom: 5px;
}

div.hack {
    height: 100px;
}

a {
    color: $main-gold;
    border-bottom: 1px dotted #443E31;
    text-decoration: none;

    &:hover {
        color: #CAC9C8;
        border-bottom: 1px solid #3F3F3F;
        text-decoration: none;
    }
}

div.navbar-collapse ul.navbar-right a {
    color: $main-gold;
    border-bottom: 0;
    text-decoration: none;
    font-size: $base-font-size * 1.25;
    font-style: italic;
    text-transform: lowercase;

    &:hover {
        color: #CAC9C8;
    }
}

a.carousel-control {
    border: 0;
}

ol.carousel-indicators > li {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.25);
    border: 1px solid black;
}

div#cassiel-carousel {
    @include bottom;
}

div.carousel-caption {
    /*bottom: 0;*/
    @include caption-in-image(1.5);
}

hr.to-pager {
    border-top: 1px solid $main-grey;
}

@mixin tiny-link {
    font-size: $base-font-size * 0.75;
    padding: 0;
    border: 0;
    border-radius: 0;
    background-color: $main-background;
}

ul.pager {
    li.previous a, li.next a {
        @include tiny-link;
        border-bottom: 1px dotted #443E31;
    }
}

div#all-tags {
    border-left: 1px solid #202020;
    padding-left: 10px;
    margin-top: 50px;

    h3 {
        @include title-font;
        font-size: $base-font-size * 1;
    }

    ul {
        margin: 0;
        -webkit-padding-start: 0;
        list-style-position: inside;

        li {
            @include tiny-link;
            border-bottom: 0;

            span.faint {
                font-size: 0.8em;
                color: $main-grey;
            }
        }
    }
}

div.float-left {
    float: left;
    margin-top: 0.5em;
    margin-right: 1.5em;
    margin-bottom: 1em;
}

code {
    color: $code-grey;
    background-color: $code-background;
}
